import React, { Component } from "react";
import {
  Button,
  Form,
  Grid,
  Header,
  Image,
  Message,
  Segment,
  Input
} from "semantic-ui-react";
import { login } from "../services/authentication";
import Router from "next/router";

export default class SignUpForm extends Component {
  handleSubmit = e => {
    e.preventDefault();
    const email = e.target.email.value;
    const password = e.target.password.value;
    login(email, password).then(resp => {
      this.props.login(resp.token);
    });
    Router.push("/");
  };

  render() {
    return (
      <Grid
        textAlign="center"
        style={{ height: "100vh" }}
        verticalAlign="middle">
        <Grid.Column style={{ maxWidth: 450 }}>
          <Image
            verticalAlign="middle"
            src="https://drive.google.com/uc?id=1l-c_jyMF1elbZKIeemM-vaFL-eRqd9xH"
          />
          <Header as="h2" color="teal" textAlign="center">
            Create a new account
          </Header>
          <br />
          <Form onSubmit={this.handleSubmit} size="large">
            <Form.Group widths="equal">
              <Form.Field inline>
                {/* <label>First name</label> */}
                <Input fluid placeholder="First name" />
              </Form.Field>
              <Form.Field inline>
                {/* <label>Last name</label> */}
                <Input fluid placeholder="Last name" />
              </Form.Field>
            </Form.Group>
            <Form.Group widths="equal">
              <Form.Field inline>
                {/* <label>Email</label> */}
                <Input fluid placeholder="Email" />
              </Form.Field>
            </Form.Group>
            <Form.Group widths="equal">
              <Form.Field inline>
                {/* <label>Email</label> */}
                <Input fluid placeholder="Password" />
              </Form.Field>
            </Form.Group>
            <Form.Group widths="equal">
              <Form.Field inline>
                {/* <label>Email</label> */}
                <Input fluid placeholder="Confirm your password" />
              </Form.Field>
            </Form.Group>

            <Button color="teal" fluid size="large">
              Create an account
            </Button>
          </Form>
          <Message>
            Already have an account? <a href="/login">Login</a>
          </Message>
        </Grid.Column>
      </Grid>
    );
  }
}
